@import "../../assets/styles/reset.scss";
@import "../../assets/styles/com.scss";
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 120px;
    .logo {
        & img {
            width: 171px;
            height: 30px;
        }
    }
    .nav {
        & a {
            font-size: 16px;
            padding: 0 15px;
            color: #000000;
        }
    }
    .right {
        display: flex;
        .skin {
            width: 116px;
            height: 36px;
            border-radius: 24px;
            border: 1px solid #000000;
            overflow: hidden;
            margin-right: 20px;
            font-size: 16px;
            font-weight: 600;
            color: #000000;
            & option {
                font-size: 16px;
            }
        }
        .language {
            width: 116px;
            height: 36px;
            background-color: #ECECEC;
            border-radius: 24px;
            overflow: hidden;
            font-size: 16px;
            font-weight: 600;
            :global {
                .ant-select-selector {
                    background-color: #ECECEC !important;
                }
            }
        }
        .wallet {
            cursor: pointer;
            width: 116px;
            height: 36px;
            background: #ECECEC;
            border-radius: 24px;
            font-weight: 600;
            color: #000000;
            text-align: center;
            line-height: 36px;
            margin-right: 20px;
        }
    }
    :global {
        .ant-select-selector {
            border: 0 !important;
            height: 36px !important;
        }
        .ant-select-selection-item {
            padding-left: 5px !important;
            line-height: 34px !important;
        }
    }
}

@media screen and (min-width:1300px) {
    .header {
        padding: 0 50px;
    }
}

.BSC {
    .active {
        color: #E5A800 !important;
    }
}

.HECO {
    .active {
        color: #009A7F !important;
    }
}
